:root {
  --blancoPrincipal: #f3f6f5;
  --rosaPrincipal: #ffafcc; 
  --doradoSuave: #fbc739; 
  --cafePrincipal: #7E5C40; 
  --cafeEspecial: #7F550E; 



  --fuenteElegante: 'fuenteElegante';
  --fuenteRegular: 'fuenteRegular';
  --fuenteExpecialLustria: 'fuenteExpecialLustria';


  --txtSize1: 38px;
  --txtSize2: 28px;
  --txtSize3: 24px;
  --txtSize4: 20px;
  --txtSize5: 16px;

}
@font-face {
  font-family: 'fuenteElegante';
  src: url('./recursos/fuentes/DancingScript-VariableFont_wght.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'fuenteRegular';
  src: url('./recursos/fuentes/PlayfairDisplay-VariableFont_wght.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'fuenteExpecialLustria';
  src: url('./recursos/fuentes/Lustria-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
